import React from 'react';
import './styles/slider-mobile.scss';
import './styles/slider-pc.scss';

function Slider({children, slide}) {

    const [currSlide, setCurrSlide] = React.useState(0);
    // const [isAnimated, setIsAnimated] = React.useState(true);

    // const timeout = React.useRef();

    function goToSlide(index) {
        // if ( currSlide == children.length - 1 && index == 0 ) {
        //     setCurrSlide(currSlide + 1);

        //     timeout.current = setTimeout(() => {
        //         setIsAnimated(false);
        //         setCurrSlide(index);
        //     }, 300);
        // } else if ( currSlide == 0 && index == children.length - 1 ) {
        //     setCurrSlide(-1);

        //     timeout.current = setTimeout(() => {
        //         setIsAnimated(false);
        //         setCurrSlide(index);
        //     }, 300);
        // } else {
        //     setCurrSlide(index);
        // }

        setCurrSlide(index);
    }

    React.useEffect(() => goToSlide(slide), [slide]);
    // React.useEffect(() => {
    //     return function() {
    //         setIsAnimated(true);
    //         clearTimeout(timeout.current);
    //     }
    // }, [currSlide]);

    return (
        <div id="slider" className="slider">
            <div className="slider--wrapper">
                <div 
                    className="slider--wrapper--container" 
                    style={{ 
                        '--slide': currSlide,
                        '--count': children.length || 1,
                    }}
                >
                    {/* { children.length > 1 && children.slice(0, 2).map((child, i) => <Slide key={i} clone={true} index={i}>{child}</Slide>) } */}
                    { 
                        children.length > 1 ? 
                        children.map((child, i) => <Slide key={i} index={i}>{child}</Slide>) :
                        <Slide>{children}</Slide>
                    }
                    {/* { children.length > 1 && children.slice(-2).map((child, i) => <Slide key={i} clone={true} index={i}>{child}</Slide>) } */}
                </div>
            </div>
        </div>
    );
}

const Slide = ({children, clone, index}) => (
    <span className={`slide slide-${index} ${clone ? 'clone' : ''}`}>
        {children}
    </span>
)

export default Slider;