import React from 'react';
import PlatformContext from '../../context/platform';
import './styles/menu-pc.scss';
import './styles/menu-mobile.scss';

const CirclePC = () => (
    <path 
        id="circle"
        d="M74.064,572.708C204.361,238.113,529.669,1,910.357,1,1405.834,1,1807.5,402.663,1807.5,898.14s-401.664,897.14-897.141,897.14c-371.069,0-689.521-225.282-826.045-546.535" 
        transform="translate(-7.14)" 
        fill="none" 
        stroke="#454956" 
        stroke-miterlimit="10" 
        stroke-width="2"
    />
);

const CircleMobile = () => (
    <g id="circle" transform="translate(0.285 -590.591)">
        <path id="Контур_1112" data-name="Контур 1112" d="M601.659,717.883q-11.546,7.409-23.521,13.942" transform="translate(-186 -126)" fill="none" stroke="#3b3b3b" stroke-width="1"/>
        <path id="Контур_1113" data-name="Контур 1113" d="M210.861,731.825q-12.713-6.936-24.874-14.815" transform="translate(-186 -126)" fill="none" stroke="#3b3b3b" stroke-width="1"/>
    </g>
);


function Menu({callback}) {
    const platform = React.useContext(PlatformContext);
    const cursorCPC = 2*(Math.PI * 875);
    const cursorCMobile = 2*(Math.PI * 385);
    const options = [
        {
            text: 'приложение',
            cursorPC: {
                width: 177,
                rotation: 0
            },
            cursorMobile: {
                width: 98,
                rotation: 0
            }
        },
        {
            text: 'мультимедия',
            cursorPC: {
                width: 186,
                rotation: 12.7
            },
            cursorMobile: {
                width: 103,
                rotation: -17
            }
        },
        {
            text: 'видеорегистратор',
            cursorPC: {
                width: 253,
                rotation: 26.2
            },
            cursorMobile: {
                width: 142.5,
                rotation: -40.25
            }
        }
    ]

    const [currOption, setCurrOption] = React.useState(2);

    React.useEffect(() => {
        callback(currOption);
    }, [currOption]);

    const timer = React.useRef();
    
    function autoSwitch() {
        const nextOption = currOption - 1 > 0 ? currOption - 1 : 0;
        setCurrOption(nextOption);
    }

    React.useEffect(() => {
        timer.current = setTimeout(autoSwitch, 7000);
        return () => { clearTimeout(timer.current); }        
    }, [currOption]);

    return (
        <div id="menu" className="menu">
            <svg viewBox={`${platform == 'pc' ? '0 0 1800 1800' : '0 0 416 85'}`}> 
                { platform == 'pc' ? <CirclePC/> : <CircleMobile/> }

                <circle 
                    id="cursor"
                    cx={platform == 'pc' ? '900' : '208'}
                    cy={platform == 'pc' ? '900' : '-310'}
                    r={platform == 'pc' ? '875' : '382'}
                    fill="rgba(200, 40, 40, .4)"
                    transform={
                        platform == 'pc' ?
                        `rotate(${158 + options[currOption]['cursorPC'].rotation} 0 0)` :
                        `rotate(${103 + options[currOption]['cursorMobile'].rotation} 208 -310)`
                    }
                    style={
                        platform == 'pc' ?
                        {
                            strokeDashoffset: cursorCPC - options[currOption]['cursorPC'].width + 'px',
                        } :
                        {
                            strokeDashoffset: cursorCMobile - options[currOption]['cursorMobile'].width,
                        }
                    }
                />
                
                <g>
                    <path 
                        id="curve" 
                        d={
                            platform == 'pc' ?
                            'M 78, 1250 a 900, 900 0 0, 1 -10, -680' :
                            'M 0, 0 a 382, 382 0 0, 0 416, 0'
                        }
                        fill="none"
                    />

                    <text id="options" dy={platform == 'pc' ? '10' : '4'}>
                        <textPath startOffset={platform == 'pc' ? '0%' : '7%'}  xlinkHref="#curve">
                            { 
                                options.map(
                                    (option, i) => <Option 
                                        key={i} 
                                        active={i === currOption}
                                        text={option.text}
                                        dx={platform == 'pc' ? '20' : '10'}
                                        onClick={() => setCurrOption(i)}
                                    />
                                ) 
                            }
                        </textPath>
                    </text>
                </g>
            </svg>
        </div>
    );
}

const Option = ({text, active, dx, onClick}) => (
    <tspan 
        dx={dx} 
        onClick={onClick} 
        className={`option option_0 ${active ? 'active' : ''}`}
    >
        {text}
    </tspan>
)

export default Menu;